.job-log-cont {
	height: calc(100% - 100px);
	top: 100px;
}
.job-log-cont>.table-container {
	padding: 10px;
}
.job-log-cont>.table-container>.card {
	min-height: 100%;
	max-height: 100%;
	min-width: 100% !important;
	max-width: 100% !important;
}
#log-filter-card {
	position: relative;
	left: 10px;
	top: 10px;
	z-index: 60 !important;
	width: 49%;
	height: 4vh;
	min-height: 5vh;
	max-height: 5vh;
}
#log-filter-card>.card-content {
	height: 100%;
}

.log-action-button {
	height: 100% !important;
	width: 150px;
	line-height: 25px;
}

.joblog-table {
	min-height: calc(100% - 40px);
	max-height: calc(100% - 40px);
}

.job-log-card>.card-content {
	padding: 0px;
	min-height: 100%;
	max-height: 100%;
}
.job-log-card>.card-content>.tablerow.header {
	background-color: #062a49;
	color:#fff;
	border-top-right-radius: 7.5px;
	border-top-left-radius: 7.5px;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(7, 14%);
}

.job-log-card>.card-content>.tablerow.header>div {
	padding: 0;
	margin: 0;
	line-height: 40px;
	font-size: 20px;
	background-color: transparent;
}

.joblog-table>.tablerow {
	height: 100px;
	display: grid;
	grid-template-columns: 14% 14% 14% 42% 14%;
	align-items: center;
	border-bottom: 1px solid #ccc;
}
.joblog-table>.tablerow:hover {
	background-color: #f9f9f9;
}
.joblog-table>.tablerow>.row-item {
	font-size: 25px;
}