#submitted-quotes-table {
	width: 95%;
	height: 95%;
	overflow: hidden;
}
#submitted-quotes-table>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
	overflow-y: auto;
}
#submitted-quotes-table>.card-content>.tablerow {
	padding: unset;
	height: fit-content;
	display: grid;
	grid-template-columns: 15% 15% 15% 20% 35%;
	width: 100%;
	border-bottom: 1px solid #ccc;
	padding: 10px;
	align-items: center;
	justify-content: center;
}
#submitted-quotes-table>.card-content>.tablerow>div {
	margin: 0px;
	padding: 0px;
	height: 100%;
}
#submitted-quotes-table>.card-content>.tablerow>.action-button {
	height: 40px;
	line-height: 40px;
}